<template>
<div class="edit-rule">
    <div class="rule-to-edit">
        <p>Правило номер {{ Key }}</p>
        <i class="material-icons closeIcon" @click="$emit('closeSelect')">clear</i>
        <div class="select-wrapper">
        <div class="input-field">
        <select class="browser-default" v-model="Discipline_Level">
            <option value="начальный">начальный</option>
            <option value="базовый">базовый</option>
            <option value="средний">средний</option>
            <option value="продвинутый">продвинутый</option>
            <option value="сложный">сложный</option>
        </select>
        <label style="transform: translateY(-20px);">Уровень усвоения дисциплины</label>
        </div>
        <div class="input-field">
        <select class="browser-default" v-model="Self_Development">
            <option value="низкий">низкий</option>
            <option value="ниже среднего">ниже среднего</option>
            <option value="средний">средний</option>
            <option value="выше среднего">выше среднего</option>
            <option value="высокий">высокий</option>
        </select>
        <label style="transform: translateY(-20px);">Стремление к саморазвитию</label>
        </div>
        <div class="input-field">
        <select class="browser-default" v-model="Responsibility">
            <option value="низкий">низкий</option>
            <option value="ниже среднего">ниже среднего</option>
            <option value="средний">средний</option>
            <option value="выше среднего">выше среднего</option>
            <option value="высокий">высокий</option>
        </select>
        <label style="transform: translateY(-20px);">Ответственность</label>
        </div>
        <div class="input-field">
        <select class="browser-default" v-model="Perseverance">
            <option value="низкий">низкий</option>
            <option value="ниже среднего">ниже среднего</option>
            <option value="средний">средний</option>
            <option value="выше среднего">выше среднего</option>
            <option value="высокий">высокий</option>
        </select>
        <label style="transform: translateY(-20px);">Усидчивость</label>
        </div>
        <div class="input-field">
        <select class="browser-default" v-model="Attentiveness">
            <option value="низкий">низкий</option>
            <option value="ниже среднего">ниже среднего</option>
            <option value="средний">средний</option>
            <option value="выше среднего">выше среднего</option>
            <option value="высокий">высокий</option>
        </select>
        <label style="transform: translateY(-20px);">Внимательность</label>
        </div>
        <div class="input-field">
        <select class="browser-default" v-model="Stress">
            <option value="низкий">низкий</option>
            <option value="ниже среднего">ниже среднего</option>
            <option value="средний">средний</option>
            <option value="выше среднего">выше среднего</option>
            <option value="высокий">высокий</option>
        </select>
        <label style="transform: translateY(-20px);">Стрессоустойчивость</label>
        </div>
        </div>
        <div class="result">
        <div class="input-field">
        <select class="browser-default" v-model="Result">
            <option value="новичок">новичок</option>
            <option value="стажер">стажер</option>
            <option value="мастер">мастер</option>
            <option value="профессионал">профессионал</option>
            <option value="эксперт">эксперт</option>
        </select>
        <label style="transform: translateY(-20px);">Статус обучаемого</label>
        </div>
        <button class="waves-effect waves-light btn" @click="editRule">Редактировать</button>
    </div>
</div>
</div>
</template>

<script>
import {ref} from 'vue'
import axios from 'axios'

const server = "http://db.19ivt.ru/";

export default {
    props: {
        rule: {}
    },
    setup(props, {emit}) {
        const Key = ref(props.rule.Key)
        const Discipline_Level = ref(props.rule.Discipline_Level)
        const Self_Development = ref(props.rule.Self_Development)
        const Responsibility = ref(props.rule.Responsibility)
        const Perseverance = ref(props.rule.Perseverance)
        const Attentiveness = ref(props.rule.Attentiveness)
        const Stress = ref(props.rule.Stress)
        const Result = ref(props.rule.Result)

        const editRule = () => {
            const obj = {
                Key: Key.value,
                Discipline_Level: Discipline_Level.value,
                Self_Development: Self_Development.value,
                Responsibility: Responsibility.value,
                Perseverance: Perseverance.value,
                Attentiveness: Attentiveness.value,
                Stress: Stress.value,
                Result: Result.value
            }
            axios.get(server + 'updateRule', {params:obj}, res => {
                if(res == "Ошибочка") {
                    M.toast({html: 'Серверная ошибка'})
                }
            });
            emit('editRuleMethod')
        }

        return {
            Discipline_Level,
            Self_Development,
            Responsibility,
            Perseverance,
            Attentiveness,
            Stress,
            Result,
            editRule,
            Key
        }
    },
}
</script>

<style lang="scss" scoped>
.edit-rule{
    top: 0;
    left: 0;
    position: fixed;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    backdrop-filter: blur(5px);
    z-index: 3;
    .rule-to-edit{
        background-color: #fff;
        position: relative;
        padding: 10px;
        .closeIcon{
            position: absolute;
            top: -15px;
            right: -15px;
            z-index: 3;
            cursor: pointer;
        }
    }
}
</style>