<template>
<div class="container">
  <div class="select-task">
     <p style="margin-right: 15px;">
      <label>
        <input name="group1" type="radio" @click="currentPage = 0" />
        <span>Ввод тестов</span>
      </label>
    </p>
    <p>
      <label>
        <input name="group1" type="radio" @click="currentPage = 1" />
        <span>Ввод базы правил</span>
      </label>
    </p>
  </div>
  <div class="test-page" v-if="currentPage === 0">
  <h2 style="margin-bottom: 60px;">Пришел тест делать, странник? Ну удачи...</h2>
  <div class="input-field col s12">
    <p>Выбери тест (свой, не чужой) [Прикрепление картинок не работает.. ждем патча, делайте вопросы без картинок]</p>
    <select v-if="this.dataThemes != null" id="sel" :key="renderKey" class="browser-default" v-model="selectedThemeKey">
      <option v-for="theme in this.dataThemes" :key="theme.Key" :value="theme.Key">{{theme.Name + " (Сложность: " + theme.TName + ")"}}</option>
    </select>
    <h5 v-else>Не подгрузились тесты, павис цикол...</h5>
  </div>
  <form ref="anyName" action="" id="frm" v-if="render" @submit.prevent="subm">
    <div class="question">
      <input placeholder="Название вопроса" id="question_name" type="text" class="validate">
      <input ref="img" type="file" placeholder="Картинка" id="Testimg" disabled>
      <label for="Testimg">Картинка вопроса</label>
      <ul>
        <li id="questionVar" v-for="item in questionCount">
          <textarea ref="var" placeholder="Вариант ответа" id="textarea1" class="materialize-textarea">{{item}}</textarea>
          <label>
            <input ref="correct" id="correct" name="group1" type="radio" />
            <span>Правильный</span>
          </label>
          <!-- <label for="files" class="waves-effect waves-light btn white-text" style="cursor:pointer; width: 150px; margin-left: 15px;">Картинка</label>
          <input id="files" type="file" style="display: none;"  class="waves-effect waves-light btn"> -->
          <input ref="img" type="file" placeholder="Картинка" id="img" style="margin-left: 10px;" disabled>
          <!-- <a class="waves-effect waves-light btn" style="width: 175px !important; margin-left: 15px;"><i class="material-icons left">cloud</i>Картинка</a> -->
        </li>
      </ul>
    </div>
    <div class="add-var" style="display: flex; align-items: center;">
      <a class="btn-floating" style="margin: 15px;"><i class="material-icons" @click="addVar">exposure_plus_1</i></a>
      <a class="btn-floating"><i class="material-icons" @click="removeVar">exposure_neg_1</i></a>
    </div>
    <button class="btn waves-effect waves-light" type="submit" name="action">Добавить вопросек
      <i class="material-icons right">send</i>
    </button>
  </form>
  <div class="row" v-if="error">
     <img class="col l6 m6 s12" src="https://lh3.googleusercontent.com/-Gc21j8Vu6Zw/YZDa8iCYjTI/AAAAAAAACyM/ydg_HmZYJpoQuuMKr6Sh1POQBTUvu4qXgCLcBGAsYHQ/w945-h600-p-k-no-nu/VZ0uE5xt2ok.jpg" alt="">
     <p class="col l6 m6 s12 flow-text">Павис цикл, чекни инет либо напиши о проблеме</p>
  </div>
  <form style="margin-top: 30px;" class="new-test card" @submit.prevent="newDiff">
      <input placeholder="Название теста" id="question_name" type="text" class="validate" v-model="newTestName">
      <select class="browser-default" v-model="newTestDifficuilty">
        <option disabled selected>Сложность теста</option>
        <option v-for="diff in this.testDifficuilties" :key="diff.Key" :value="diff.Key">{{diff.TName}}</option>
      </select>
    <button style="margin-top: 15px;" class="btn waves-effect waves-light" type="submit" name="action">Добавить тест
      <i class="material-icons right">send</i>
    </button>
  </form>
  </div>

  <div class="rules-page" v-if="currentPage === 1">
    <h3 class="center">Добавление правила в базу правил</h3>
    <div class="container-inner" style="margin-top: 50px;">
    <div class="select-wrapper">
    <div class="input-field">
      <select class="browser-default" v-model="disciplineLevel">
        <option value="начальный">начальный</option>
        <option value="базовый">базовый</option>
        <option value="средний">средний</option>
        <option value="продвинутый">продвинутый</option>
        <option value="сложный">сложный</option>
      </select>
      <label style="transform: translateY(-20px);">Уровень усвоения дисциплины</label>
    </div>
      <div class="input-field">
      <select class="browser-default" v-model="selfDevelopement">
        <option value="низкий">низкий</option>
        <option value="ниже среднего">ниже среднего</option>
        <option value="средний">средний</option>
        <option value="выше среднего">выше среднего</option>
        <option value="высокий">высокий</option>
      </select>
      <label style="transform: translateY(-20px);">Стремление к саморазвитию</label>
    </div>
      <div class="input-field">
      <select class="browser-default" v-model="responsibility">
        <option value="низкий">низкий</option>
        <option value="ниже среднего">ниже среднего</option>
        <option value="средний">средний</option>
        <option value="выше среднего">выше среднего</option>
        <option value="высокий">высокий</option>
      </select>
      <label style="transform: translateY(-20px);">Ответственность</label>
    </div>
      <div class="input-field">
      <select class="browser-default" v-model="perseverance">
        <option value="низкий">низкий</option>
        <option value="ниже среднего">ниже среднего</option>
        <option value="средний">средний</option>
        <option value="выше среднего">выше среднего</option>
        <option value="высокий">высокий</option>
      </select>
      <label style="transform: translateY(-20px);">Усидчивость</label>
    </div>
      <div class="input-field">
      <select class="browser-default" v-model="attentiveness">
        <option value="низкий">низкий</option>
        <option value="ниже среднего">ниже среднего</option>
        <option value="средний">средний</option>
        <option value="выше среднего">выше среднего</option>
        <option value="высокий">высокий</option>
      </select>
      <label style="transform: translateY(-20px);">Внимательность</label>
    </div>
    <div class="input-field">
      <select class="browser-default" v-model="stress">
        <option value="низкий">низкий</option>
        <option value="ниже среднего">ниже среднего</option>
        <option value="средний">средний</option>
        <option value="выше среднего">выше среднего</option>
        <option value="высокий">высокий</option>
      </select>
      <label style="transform: translateY(-20px);">Стрессоустойчивость</label>
    </div>
    </div>
    <div class="result">
      <div class="input-field">
      <select class="browser-default" v-model="result">
        <option value="новичок">новичок</option>
        <option value="стажер">стажер</option>
        <option value="мастер">мастер</option>
        <option value="профессионал">профессионал</option>
        <option value="эксперт">эксперт</option>
      </select>
      <label style="transform: translateY(-20px);">Статус обучаемого</label>
    </div>
    <button @click="submitRule" class="waves-effect waves-light btn">Сохранить</button>
    </div>
    </div>

  </div>
</div>
<table v-if="currentPage == 1">
  <thead>
    <tr>
      <th>Индекс</th>
      <th>Уровень усвоения дисциплины</th>
      <th>Стремление к саморазвитию</th>
      <th>Ответственность</th>
      <th>Усидчивость</th>
      <th>Внимательность</th>
      <th>Стрессоустойчивость</th>
      <th>Статус обучаемого</th>
    </tr>
  </thead>
  <tbody v-if="needRules">
    <rulesItem v-for="rule in this.rulesList" :key="rule.Key" :ruleItem="rule" @ruleSelected="ruleSelected(rule)"/>
  </tbody>
</table>
<div v-if="editingRule != null" class="edit-modal">
  <edit-rule :rule="editingRule" @closeSelect="closeSelect" @editRuleMethod="editRuleMethod"></edit-rule>
</div>
</template>

<script>
import rulesItem from "@/components/rulesItem.vue"
import editRule from "@/components/editRule.vue"
import _ from 'lodash'
import axios from 'axios'
const server = "http://db.19ivt.ru/";
export default {
data() {
  return {
    needRules: true,
    editRule: false,
    questionCount: ['','','',''],
    error: false,
    render: true,
    dataThemes : null,
    selectedThemeKey : null,
    renderKey: 0,
    gotCorrect: false,
    newTestName : null,
    newTestDifficuilty: null,
    testDifficuilties: null,
    disciplineLevel: '',
    selfDevelopement: '',
    responsibility: '',
    perseverance: '',
    attentiveness: '',
    stress: '',
    result: '',
    currentPage: -1,
    rulesList: null,
    editingRule: null
  }
},
components: { rulesItem, editRule },
methods: {
  editRuleMethod() {
    this.editingRule = null
    this.needRules = false
    this.needRules = true
  },
  closeSelect() {
    this.editingRule = null
  },
  ruleSelected(rule) {
    this.editingRule = rule
  },
  submitRule () {
    if(this.disciplineLevel != '' && this.selfDevelopement != '' && this.responsibility != '' && this.perseverance != '' && this.attentiveness != '' && this.stress != '' && this.result != '') {
      const rule = { //отправить это
        disciplineLevel: this.disciplineLevel,
        selfDevelopment: this.selfDevelopement,
        responsibility: this.responsibility,
        perseverance: this.perseverance,
        attentiveness: this.attentiveness,
        stress: this.stress,
        result: this.result
      }
      axios.get(server + 'postRule', {params:rule}, res => {
        if(res == "Ошибочка") {
          M.toast({html: 'Серверная ошибка'})
        }
      });
      M.toast({html: 'Отправлено'})
        this.disciplineLevel.value = '';
        this.selfDevelopement.value = '';
        this.responsibility.value = '';
        this.perseverance.value = '';
        this.attentiveness.value = '';
        this.stress.value = '';
        this.result.value = '';
      // console.log(rule);
      axios.get(server + `getRuleList?sorted=${false}`).then(res => {
      if(res.data == "Ошибочка") {
        M.toast({html: "Ошибка при загрузке листа заполненных правил"})
      }
      this.$store.commit('getRules', res.data)
    })
    }
    else { M.toast({html: 'Вы не выбрали какое то условие'}) }
  },
  serverLogger(log) {
    console.log("Сервер вернул: " + log);
  },
  addVar () {
    if(this.questionCount.length <= 7)
    this.questionCount.push('')
    else M.toast({html: 'Максимум 8 вариантов'})
  },
  removeVar () {
    if(this.questionCount.length != 4)
    this.questionCount = this.questionCount.slice(0, -1)
    else M.toast({html: 'Должно быть минимум 4 варианта'})
  },
  async newDiff () {
    if(this.newTestDifficuilty == null || this.newTestName == null) {
      M.toast({html: 'Вы что-то не ввели...'})
    }
    else {
      const newTestData = {
        difficulty : this.newTestDifficuilty,
        name : this.newTestName
      }
      axios.get(server + "submitTest", {params:newTestData});
      M.toast({html: 'Вроде добавил тест'})

      axios.get(server + "getTopicList").then(res => {
      if(res.data == "Ошибочка") {
        this.serverLogger(res.data);
        this.$data.error = true;
        this.error = true;
      }
      this.error = false;
      //this.thisThemes = res.data
      this.$store.commit('getThemes', res.data)
    });
    }
  },
  async subm () {
    //new Audio("pn.mp3").play();
    if(this.selectedThemeKey && document.querySelector('#question_name').value !== '' && document.querySelector('#question_name').value != '') {
    const questionName = document.querySelector('#question_name').value
    const vars = document.querySelectorAll('#questionVar')
    const varArr = []
    vars.forEach(element => {
      const varName = element.querySelector('#textarea1').value
      const correct = element.querySelector('#correct').checked
      if(correct == true) this.gotCorrect = true
      const img = element.querySelector('#img').value
      //const testImg = document.querySelector('Testimg').value
      const varElement = {
        varName,
        correct,
        img : null
      }
      varArr.push(varElement)
    });
      if(this.gotCorrect) {
      const postInfo = { //объект для экспорта в базу
          questionName,
          varArr,
          Testkey : this.selectedThemeKey,
          testImg : null
      }

      axios.get(server + "submitQuestion", {params:postInfo});

      // const res = await 'MethodName(' {
      //  провод для регистрации (вызвать метод для отправки теста из папки provoda в /src и отправить туда все данные)
      // })
      // console.log(res.data)
      // this.questionCount = null
      // console.log(this.questionCount)

      this.questionCount = ['','','','']
      this.$refs.anyName.reset(); // This will clear that form
      M.toast({html : 'Вопрос отправлен (но это не точно)'})
      this.gotCorrect = false;
      }
      else M.toast({html: 'Нужно выбрать правильный ответ'})
    }
    else{
      M.toast({html: 'Вы что то не ввели...'})
    }
  },
  log() {
    console.log(this.dataThemes)
  },

  async getTests() {
    axios.get(server + "getDiffList").then(res => {
       if(res.data == "Ошибочка") {
         this.serverLogger(res.data);
         this.$data.error = true;
         this.error = true;
       }
       this.error = false;

       this.$store.commit('getDiffs', res.data)
     });
  }
},
mounted() {
  M.AutoInit()
},

beforeMount() {
  axios.get(server + "getDiffList").then(res => {
       if(res.data == "Ошибочка") {
         this.serverLogger(res.data);
         this.$data.error = true;
         this.error = true;
       }
       this.error = false;

       this.$store.commit('getDiffs', res.data)
     });
  axios.get(server + "getTopicList").then(res => {
      if(res.data == "Ошибочка") {
        this.serverLogger(res.data);
        this.$data.error = true;
        this.error = true;
      }
      this.error = false;
      //this.thisThemes = res.data
      this.$store.commit('getThemes', res.data)
    });

  axios.get(server + `getRuleList?sorted=${true}`).then(res => {
    if(res.data == "Ошибочка") {
      M.toast({html: "Ошибка при загрузке листа заполненных правил"})
    }
    this.$store.commit('getRules', res.data)

  })

},

watch: {
  '$store.state.storeThemes': {
    handler: function (nv) {
      const data = JSON.parse(JSON.stringify(nv))
      this.dataThemes = data
    },
    immediate: true
  },
  '$store.state.themeDifficuities': {
    handler: function (nv) {
      const data = JSON.parse(JSON.stringify(nv))
      this.testDifficuilties = data
    },
    immediate: true
  },
  '$store.state.rulesList': {
    handler: function (nv) {
      const data = JSON.parse(JSON.stringify(nv))
      //console.log(data);
      this.rulesList = data
    },
    immediate: true
  },
  dataThemes(newvalue) {
    this.$forceUpdate();
  },
  selectedThemeKey(newval) {
    console.log(newval)
  }
}



}

</script>

<style lang="scss">
@media screen and (max-width: 965px) {
  table {
    display: none;
  }
}

.edit-modal{
  width: 100%;
  height: 100%;
}

.select-task{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
li {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
img{
  margin: 10px;
  max-width: 400px;
  width: 100%;
}
.pavis{
  display: flex;
}
.new-test{
  padding: 15px;
}

.select-wrapper{
  display: flex;
  flex-direction: column;
}
.container-inner{
  display: flex;
  justify-content: space-around;
  align-items: center;
}

select {
  min-width: 250px;
}

@media screen and (max-width: 510px) {
  .container-inner {
    flex-direction: column;
  }
}

</style>
