import { createStore } from 'vuex'

export default createStore ({
    state() {
        return {
            storeThemes : null,
            themeDifficuities : null,
            rulesList : null
        }
    },
    mutations: {
        getThemes (state, obj) {
            this.state.storeThemes = obj
        },
        getDiffs (state, obj) {
            this.state.themeDifficuities = obj
        },
        resetTests(state) {
            this.state.storeThemes = null
        },
        getRules(state, obj) {
            this.state.rulesList = obj
        }
    },
    getters: {
        storeThemes: state => { return state.storeThemes },
        getThemeDiffs: state => { return state.themeDifficuities },
        getRulesList: state => { return state.rulesList }
    }
})
